import React, {useRef} from 'react'
import  Helmet from '../components/Helmet/Helmet'
import CommonSection from '../components/UI/common-section/CommonSection'
import { Container, Row, Col } from 'reactstrap'
import {Link} from 'react-router-dom'

const Login = () => {

    const loginNameRef = useRef()
    const loginPasswordRef = useRef()

    const submitHandler = e =>{
        e.preventDefault()
    };


    return (
        <Helmet title='Login'>
         <CommonSection title='Login' />
         <section>
          <Container>
           <Row>
            <Col lg='6' md='6' sm='12' className='m-auto text-center'>
             <form className='form mb-5' onSubmit={submitHandler}>
              <div className='form__group'>
               <input type='email' placeholder='Email' required ref={loginNameRef} />
              </div>
              <div className='form__group'>
               <input type='password' placeholder='Password' required ref={loginPasswordRef} />
              </div>
              <button className='addToCart__btn'>Login</button>
              <p>
                <Link to='/emailverifypage'>
                 Forgot Password
               </Link>
              </p>
             </form>
             <div className='Register__link-btn'>
              <label>Don't have an account?  </label> 
              <Link to='/register'>
               Create account
             </Link>
             </div>
            </Col>
           </Row>
          </Container>
         </section>
        </Helmet>
    )
}

export default Login
